import React, { useContext, useState } from 'react'
import { AuthContext } from '../auth/AuthContext';
import { ChatContext } from '../context/chat/ChatContext';
import { SocketContext } from '../context/SocketContext';

export const SendMessage = () => {

    const [mensaje, setMensaje] = useState("");
    const {socket} = useContext(SocketContext)
    const {auth} = useContext(AuthContext)
    const {chatState} = useContext(ChatContext)


    const handleOnChange = ( {target} )=>{

        setMensaje(target.value)

    }

    const handleOnSubmit = (e)=>{

        e.preventDefault();

        if( mensaje.length ===0 ){
            return;
        }

        setMensaje("");
        
         // Todo Emitir un mensaje de sockets para enviar el mensaje
        // {
        //     de: // UID del usuario enviando el mensaje
        //     para: //UID del usuario que recibe el mensaje
        //     mensaje: // lo que queremos enviar
        // }

        socket.emit('mensaje-personal',{
            de:auth.uid,
            para: chatState.chatActivo,
            mensaje
        })


        //todo hacer el dispatch de el mensaje...
    }

    return (
        <form  onSubmit={ handleOnSubmit } action="">
            <div className="type_msg row">
                <div className="input_msg_write col-sm-9">
                    <input 
                        type="text" 
                        className="write_msg" 
                        placeholder="Mensaje..."
                        value= { mensaje } 
                        onChange = { handleOnChange }
                    />
                </div>
                <div className="col-sm-3 text-center">
                    <button className="msg_send_btn mt-3" type="submit">
                        enviar
                    </button>
                </div>
            </div>
        </form>
    )
}
